@use '../helpers/' as *;

.modal__container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  backdrop-filter: blur(4px);
  background-color: transparent;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.4s ease, visibility 0.4s ease;
  z-index: 10;
  pointer-events: none;

  &.active {
    opacity: 1;
    visibility: visible;
  }

  .modal__content {
    max-width: 80%;
    padding-inline: fluid(30, 10);
    padding-block: fluid(30, 20);
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: rem(20);
    background-color: var(--background-additional);
    border-radius: var(--radius);
    border: var(--border);
    box-shadow: var(--shadow);
    transform: scale(0.8) translateY(-20px);
    transition: transform 0.4s cubic-bezier(0.4, 1.2, 0.5, 1);
  }
    
  .modal__animation {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &.active .modal__content {
    transform: scale(1) translateY(0);
  }

  &.closing .modal__content {
    transform: scale(0.8) translateY(-20px);
    opacity: 0;
  }
}
