@use '../helpers/' as *;

/* Houdini: плавная анимация угла conic-gradient */
@property --angle {
  syntax: '<angle>';
  initial-value: 0deg;
  inherits: false;
}

@keyframes spin {
  from { --angle: 0deg; }
  to   { --angle: 360deg; }
}

.header {
  position: fixed;
  inset-inline: 0;
  top: 0;

  width: 100%;
  background-color: transparent;
  box-shadow: none;

  transition:
    background-color 0.8s cubic-bezier(0.64, 0.04, 0.26, 0.87),
    box-shadow 0.8s ease-in-out;

  z-index: 3;

  &.is-active {
    background-color: var(--background-basic);
    box-shadow: var(--shadow);
  }

  &__body {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-block: rem(20);

    @include mobile {
      padding-block: rem(16);
    }
  }

  &__content {
    display: flex;
    align-items: center;
    column-gap: 1.5rem;
  }

  &__select {
    text-transform: uppercase;
  }

  /* ===============================
     Navigation
     =============================== */
  &__menu {
    /* desktop — обычный nav */

    @include laptop {
      position: fixed;
      inset-inline: 0;
      top: 0;

      width: 100%;

      height: 100vh;      // fallback
      min-height: 100vh;

      @supports (height: 1svh) {
        height: 100svh;
      }
      @supports (height: 1dvh) {
        height: 100dvh;
      }

      display: flex;
      align-items: center;
      justify-content: center;

      background-color: var(--background-additional);

      transform: translate3d(-100%, 0, 0);
      transition: transform 0.4s cubic-bezier(0.64, 0.04, 0.26, 0.87);
      will-change: transform;

      padding-top: env(safe-area-inset-top);

      z-index: 2;

      &.is-active {
        transform: translate3d(0, 0, 0);
      }
    }
  }

  &__menu-list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    column-gap: 1rem;

    @include laptop {
      flex-direction: column;
      flex-wrap: nowrap;
      row-gap: 1rem;

      padding-block: rem(34);
      padding-inline: rem(70);

      overflow-y: auto;
      -webkit-overflow-scrolling: touch;
      overscroll-behavior: contain;
    }
  }

  // &__menu-item {
  //   /* intentionally empty */
  // }

  /* ===============================
     Menu Link (core of the task)
     =============================== */
  &__menu-link {
    display: inline-flex;
    align-items: center;
    justify-content: center;

    padding: rem(12) rem(12);
    text-align: center;

    border-radius: var(--radius-small);

    /* толщина рамки */
    border: rem(2) solid transparent;

    cursor: pointer;
    transition: var(--transition-link);

    /* -------- Safari / iOS critical fixes -------- */
    background-origin: padding-box, border-box;
    background-clip: padding-box, border-box;

    -webkit-mask-image: -webkit-radial-gradient(white, black);
    mask-image: radial-gradient(white, black);

    transform: translateZ(0);
    backface-visibility: hidden;
    /* --------------------------------------------- */

    /* НЕАКТИВНОЕ СОСТОЯНИЕ */
    background:
      linear-gradient(transparent, transparent) padding-box;

    &:focus {
      outline: none;
    }

    /* HOVER / TOUCH-ACTIVE */
    @include hover {
      background:
        linear-gradient(
          var(--background-basic),
          var(--background-basic)
        ) padding-box,
        conic-gradient(
          from var(--angle),
          transparent 22%,
          var(--elements-accent),
          var(--elements-accent-additional)
        ) border-box;

      animation: spin 0.8s linear 1;
    }

    /* KEYBOARD FOCUS */
    // &:focus-visible {
    //   background:
    //     linear-gradient(
    //       var(--background-basic),
    //       var(--background-basic)
    //     ) padding-box,
    //     conic-gradient(
    //       from var(--angle),
    //       transparent 22%,
    //       var(--elements-accent),
    //       var(--elements-accent-additional)
    //     ) border-box;

    //   animation: spin 0.8s linear 1;
    // }

    /* ACTIVE LINK */
    &.is-link-active {
      pointer-events: none;

      background:
        linear-gradient(
          var(--background-basic),
          var(--background-basic)
        ) padding-box,
        conic-gradient(
          from var(--angle),
          transparent 22%,
          var(--elements-accent),
          var(--elements-accent-additional)
        ) border-box;

      animation: none;
    }
  }

  /* ===============================
     Burger
     =============================== */
  &__burger-button {
    position: relative;
    z-index: 4;
  }
}