.reviews {
  &__top {
    margin-bottom: fluid(60, 40);
    display: flex;
    align-items: center;
    justify-content: space-between;
    column-gap: fluid(30, 16);
  }

  &__title {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    column-gap: fluid(20, 10);
    row-gap: rem(20);
    min-width: 0;
  }

  &__content {
    width: 80%;
    margin: 0 auto;
    padding-inline: fluid(30, 10);
    padding-block: fluid(30, 20);
    position: relative;

    background-color: var(--background-additional);
    border: var(--border);
    border-radius: var(--radius);
    transition: var(--transition-with-border);

    @include laptop {
      width: 100%;
    }

    &::after {
      content: '';
      position: absolute;
      top: fluid(24, 20);
      left: fluid(30, 10);

      background-image: url('../img/svg/quotes.svg');
      background-repeat: no-repeat;
      background-position: center center;
      background-size: contain;

      @include square(fluid(64, 36));

      pointer-events: none;
      user-select: none;
      opacity: 0.9;

      // ниже контента
      z-index: 0;
    }
  }

  &__slider-wrapper {
    width: 100%;
    padding-left: fluid(94, 56);
    padding-right: fluid(30, 20);
    position: relative;
    overflow: hidden;
    // высоту задаёт JS (bindSwiperHeight)
    transition: height var(--transition-duration) ease-in-out;
    
    &.is-height-animating {
      will-change: height;
    }

    isolation: isolate;
    touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
    z-index: 1;

    @include mobile {
      padding-left: 0;
      margin-top: rem(50);
    }

    .swiper-scrollbar-vertical,
    .swiper-scrollbar {
      position: absolute;
      right: 0;
      bottom: 0;
      top: 0;
      width: 4px;
      border-radius: 2px;
      background-color: var(--elements-accent-pale-more);
      z-index: 2;
      pointer-events: auto;
    }

    .swiper-scrollbar-drag {
      background-color: var(--elements-accent);
      border-radius: 2px;
      z-index: 3;
    }
  }

  &__slider-list {
    height: auto !important;
    min-height: 0;
  }

  &__slider-item {
    position: relative;
    // JS задаёт одинаковую высоту всем слайдам
    height: auto;
    min-height: 0;
    display: flex;
    flex-direction: column;
    row-gap: fluid(40, 20);
    overflow-wrap: anywhere;
    word-break: break-word;
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.4s ease-in-out;
    
    &.swiper-slide-active {
      opacity: 1;
      pointer-events: auto;
    }
  }

  &__text {
    margin: 0;
    max-width: 100%;
  }

  &__author {
    margin-top: auto; // прижат к низу при фиксированной высоте
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    align-self: flex-end;
    row-gap: rem(6);

    &-position {
      color: var(--text-additional);
      font-size: 14px;
      line-height: 1.35;
    }
  }

  &__star {
    position: absolute;
    @include square(rem(24));
    background-size: contain;
    background-repeat: no-repeat;

    opacity: 0;
    transform: translateY(0) rotate(0deg);

    animation: starFly var(--duration, 2s) ease-out forwards;
    animation-delay: var(--delay, 0ms);

    will-change: transform, opacity;
    z-index: 4;
    pointer-events: none;
  }

  @media (prefers-reduced-motion: reduce) {
    &__slider-wrapper {
      transition: none;
    }

    &__slider-item {
      transition: none;
    }

    &__star {
      animation: none;
      opacity: 0;
    }
  }
}
