.circle-btn {
  position: relative;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-basic);
  background-color: var(--elements-accent);
  overflow: hidden;
}
.circle-btn svg {
  width: 24px;
  height: 24px;
  pointer-events: none;
  position: relative;
  z-index: 1;
}
.circle-btn path {
  fill: currentColor;
}
.circle-btn::before {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: inherit;
  background: linear-gradient(to bottom, var(--elements-accent-extra), var(--elements-accent));
  opacity: 0;
  transition: opacity 0.4s ease-in-out;
  pointer-events: none;
}
@media (any-hover: hover) {
  .circle-btn:hover::before {
    opacity: 1;
  }
}
@media (any-hover: none) {
  .circle-btn:active::before {
    opacity: 1;
  }
}

.circle-btn:focus-visible,
.services__article-button:focus-visible {
  outline: 2px solid var(--text-additional);
  outline-offset: 4px;
  border-radius: 50%;
}

[role=tab][aria-selected=true] {
  color: var(--text-basic);
  background-color: var(--elements-accent);
  pointer-events: none;
}

[role=tabpanel][hidden=hidden] {
  display: none;
}

.fade-up {
  opacity: 0;
  transform: translate3d(0, 40px, 0);
  transition: opacity 0.75s cubic-bezier(0.22, 1, 0.36, 1), transform 0.9s cubic-bezier(0.34, 1.56, 0.64, 1);
  will-change: opacity, transform;
}
.fade-up.faded {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}
@media (width <= 47.99875rem) {
  .fade-up {
    transform: translate3d(0, 25px, 0);
  }
  .fade-up.faded {
    transform: translate3d(0, 0, 0);
  }
}

.move-in {
  opacity: 0;
  transform: translate3d(-100%, 0, 0);
  transition: opacity 0.75s cubic-bezier(0.22, 1, 0.36, 1), transform 0.9s cubic-bezier(0.22, 1, 0.36, 1);
  will-change: opacity, transform;
}
.move-in.moved {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}

/* Дополнительно: поддержка stagger через data-delay */
[data-delay] {
  transition-delay: attr(data-delay s, 0s);
}

:root {
  --text-basic: #fffefe;
  --text-additional: #b6bad3;
  --text-placeholder: #b6bad3be;
  --background-basic: #0f1123;
  --background-additional: #14162e;
  --background-form-field: #191d3b;
  --elements-accent: #5847eb;
  --elements-accent-pale: #5747ebb1;
  --elements-accent-pale-more: #5747eb7b;
  --elements-additional: #363c7d;
  --elements-accent-additional: #0f145d;
  --elements-accent-extra: #0891b2;
  --elements-accent-extra2: #5ed8f7;
  --border-color: #444866;
  --border-form-color: #2b3045;
  --border-hover: #606690;
  --border: 0.125rem solid var(--border-color);
  --border-form: 0.0625rem solid var(--border-form-color);
  --radius: clamp(0.375rem, 0.3125rem + 0.3125vw, 0.625rem);
  --radius-small: 0.375rem;
  --error: #c115df;
  --slide-transition: cubic-bezier(0.4, 0, 0.2, 1);
  --gradient: linear-gradient(to right, var(--elements-accent) 55%, var(--elements-accent-additional) 126%);
  --text-gradient: linear-gradient(
    to right,
    var(--elements-accent-extra),
    var(--elements-accent-pale),
    var(--elements-accent),
    var(--elements-additional),
    var(--elements-accent-additional),
    var(--elements-accent-extra)
  );
  --shadow: 0 0.25rem 0.5rem rgba(9, 11, 11, 0.5);
  --font-family-basic: 'Poppins Light', sans-serif;
  --font-family-titles: 'Poppins Bold', sans-serif;
  --container-width: 100rem;
  --container-padding-x: 1.25rem;
  --container-padding-x-v: 3.75rem;
  --transition-duration: 0.3s;
  --transition-link: background-color var(--transition-duration) ease-in-out, color var 0.4s ease-in-out;
  --transition-with-border: background-color var(--transition-duration) ease-in-out, border 0.4s ease-in-out;
  --slider-height: clamp(25rem, 24.375rem + 3.125vw, 27.5rem);
  --slider-height-mobile: clamp(18.75rem, 17.1875rem + 7.8125vw, 25rem);
  --100vw: calc(100vw - var(--scrollbar-width));
}
@media (width <= 90.06125rem) {
  :root {
