@keyframes line-animation {
  to {
    stroke-dashoffset: 0;
  }
}
@keyframes spin {
  from {
    --angle: 0deg;
  }
  to {
    --angle: 360deg;
  }
}
@keyframes boxShadow {
  to {
    box-shadow: var(--shadow);
  }
}
@keyframes rotating-icon {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes flicker {
  to {
    mask-position: 50% 50%, 0 50%;
  }
}
@keyframes pulse {
  0% {
    transform: scale(0.15);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: scale(1);
    opacity: 0;
  }
}
@keyframes animate-gradient {
  to {
    background-position: 200%;
  }
}
@keyframes image-left {
  0% {
    transform: rotateY(-5deg) scaleX(0.9);
  }
  100% {
    transform: rotateY(0deg) scaleX(1);
  }
}
@keyframes image-right {
  0% {
    transform: rotateY(0deg) scaleX(1);
  }
  100% {
    transform: rotateY(5deg) scaleX(0.9);
  }
}
@keyframes starFly {
  0% {
    transform: translateY(0) rotate(0deg);
    opacity: 0;
  }
  10% {
    opacity: 1; /* быстро появляется */
  }
  50% {
    opacity: 1; /* летит полностью видимая */
  }
  60% {
    opacity: 0.8; /* начинает исчезать чуть раньше конца */
  }
  75% {
    opacity: 0.5;
  }
  90% {
    opacity: 0.2;
  }
  95% {
    opacity: 0.1;
  }
  100% {
    transform: translateY(calc(-1 * var(--fly-distance, 200px))) rotate(360deg);
    opacity: 0;
  }
}
