@use '../helpers/' as *;

.scrollup {
  position: fixed;
  @include square(rem(50));
  bottom: rem(26);
  right: rem(26);
  border-radius: 50%;
  box-shadow: var(--shadow);
  z-index: 2;

  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: translateY(rem(40)); // немного ниже изначально
  transition:
    opacity var(--transition-duration) ease-in-out,
    visibility 0.4s ease-in-out,
    transform var(--transition-duration) ease-in-out,
    bottom var(--transition-duration) ease-in-out;

  svg {
    rotate: -90deg;
  }

  &.show-scroll {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
    transform: translateY(0);
  }

  &.lifted {
    bottom: calc(rem(26) + var(--footer-lift, rem(0)));
    transform: translateY(-rem(40));
  }

  @include mobile {
    right: rem(16);
    bottom: rem(16);

    &.lifted {
      bottom: calc(rem(16) + var(--footer-lift, rem(0)));
    }
  }
}
