@use './functions' as *;

$desktop: 1440.98;
$laptop: 1023.98;
$mobile: 767.98;
$mobile-s: 480.98;

@mixin desktop-above {
  @media (width > rem($desktop)) {
    @content;
  }
}

@mixin desktop {
  @media (width <= rem($desktop)) {
    @content;
  }
}

@mixin laptop-above {
  @media (width > rem($laptop)) {
    @content;
  }
}

@mixin laptop {
  // 30em <= width <= 50em
  @media (width <= rem($laptop)) {
    @content;
  }
  // @media (width <= rem($laptop)) and (width > rem($mobile)) {
  //   @content;
  // }
}

@mixin mobile-above {
  @media (width > rem($mobile)) {
    @content;
  }
}

@mixin mobile {
  @media (width <= rem($mobile)) {
    @content;
  }
  // @media (width <= rem($mobile)) and (width > rem($mobile-s)) {
  //   @content;
  // }
}

@mixin mobile-s-above {
  @media (width > rem($mobile-s)) {
    @content;
  }
}

@mixin mobile-s {
  @media (width <= rem($mobile-s)) {
    @content;
  }
}

@mixin hover($hasActiveEffectOnMobile: true) {
  @media (any-hover: hover) {
    &:hover {
      @content;
    }
  }

  @if ($hasActiveEffectOnMobile) {
    @media (any-hover: none) {
      &:active {
        @content;
      }
    }
  }
}
