@use '../helpers/' as *;

.field {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  row-gap: rem(10);

  @include mobile {
    row-gap: rem(10);
  }

  &.error {
    .field__control {
      border-color: var(--error);
    }

    // .field__hint {
    //   color: var(--error);
    //   visibility: visible;
    //   opacity: 1;
    // }
  }

  &__label {
    // position: relative;
    &[data-required="true"]::after {
      content: '*';
      color: var(--elements-accent);
      margin-left: 0.2em;
      font-weight: bold;
    }
  } 

  &__control {
    width: 100%;
    padding: rem(12.42) rem(12);
    background-color: var(--background-form-field);
    border: var(--border-form);
    border-radius: var(--radius-small);
    color: var(--text-basic);
    transition: var(--transition-with-border);

    &::placeholder {
      color: var(--text-placeholder);
      transition: color 0.2s ease-out;
    }

    // &:user-invalid {
    //   border-color: var(--error);
    // }

    // &:user-valid {
    //   border-color: var(--border-hover);
    //   background-color: var(--background-additional);
    // }

    @include laptop {
      padding-inline: rem(10);
    }

    @include hover {
      background-color: var(--background-additional);
      border-color: var(--border-hover);
    }

    
    &:focus {
      outline: none;
      border-color: var(--border-hover);
      background-color: var(--background-additional);
      border-radius: var(--radius-small);
    }

    &:focus {
      &::placeholder {
        color: transparent;
      }
    }
    &.error {
      border-color: var(--error);
    }
  }

  &__hint {
    color: var(--error);
    // @include fluid-text(14, 13);
    font-size: rem(14);
    visibility: hidden;
    opacity: 0;
    transition: opacity var(--transition-duration) ease-in-out, visibility var(--transition-duration) ease-in-out;

    &.visible {
      visibility: visible;
      opacity: 1;
    }
  }
}

textarea.field__control {
  resize: vertical;
  min-height: rem(144);
  padding-block: 1rem;

  @include mobile {
    min-height: rem(160);
  }
}
