@use '../helpers/' as *;

.contact {
  &__body {
    width: 88%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 2fr;
    // column-gap: rem(100);
    column-gap: fluid(100, 60);
    
    @include laptop {
      width: 100%;
      column-gap: fluid(60, 40);
    }

    @include mobile {
      grid-template-columns: 1fr;
      row-gap: fluid(60, 40);
    }
  }

  &__article {
    display: grid;
    grid-template-columns: 1fr;
    align-self: start;
    row-gap: fluid(40, 20);

    @include mobile {
      grid-template-columns: 1fr 1fr;
      column-gap: fluid(60, 40);
    }

    @media (width <= rem(539)) {
      grid-template-columns: 1fr;
      row-gap: fluid(40, 20);
    }
  }

  &__article-image-wrapper {
    display: grid;
    row-gap: rem(12);

    @include mobile {
      align-self: end;
      margin: 0 auto;
    }

    @media (width <= rem(539)) {
      margin: 0;
    }
  }

  &__article-content {
    display: grid;
    grid-template-columns: 1fr;
    row-gap: fluid(40, 20);
  }

  &__article-title {
    padding-left: calc(0.8rem + 0.6rem);
    text-transform: uppercase;
    text-box-trim: trim-start;
    text-box-edge: cap alphabetic;
    order: 2;
  }

  &__article-image-box {
    max-width: rem(280);
    max-height: rem(280);
    padding: 0.12rem;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    border-radius: 50%;
    overflow: hidden;
    // cursor: pointer;

    &::after {
      width: 100%;
      height: 100%;
      content: '';
      position: absolute;
      background-image: conic-gradient(
        from var(--angle),
        transparent 25%,
        var(--elements-accent),
        var(--elements-accent-additional)
      );
    }
    @include hover {
      &::after {
        animation: 1s spin linear 1;
      }

      .contact__name {
        bottom: 0;
      }
    }

    @include laptop {
      max-width: rem(226);
      max-height: rem(226);
    }

    @include mobile {
      max-width: rem(220);
      max-height: rem(220);
    }
  }

  &__article-image {
    width: 100%;
    height: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    background-color: var(--background-basic);
    border-radius: 50%;
    overflow: hidden;
    z-index: 1;
  }

  &__picture {
    width: 80%;
    position: relative;
  }

  &__image {
    mix-blend-mode: lighten;
  }
}
