button {
  padding: 0;
  text-decoration: none;
  color: inherit;

  &:disabled {
    cursor: not-allowed;
    opacity: 0.5;
  }
}


a {
  color: var(--text-additional);

  @include hover {
    color: var(--text-basic);
  }

  &[class] {
    text-decoration: none;
  }
}

a:focus,
button:focus,
input:focus,
textarea:focus,
select:focus,
summary:focus {
  outline: none
}

a:focus-visible,
button:focus-visible,
input:focus-visible,
textarea:focus-visible,
select:focus-visible,
summary:focus-visible {
  outline: rem(2) solid var(--text-additional);
  outline-offset: rem(6);
  border-radius: rem(2);
  transition-duration: 0s !important;
}

::-webkit-scrollbar {
  width: rem(4);
  height: rem(4);
}

::-webkit-scrollbar-track {
  background: var(--background-additional);
}

::-webkit-scrollbar-thumb {
  height: rem(20);
  background: linear-gradient(to bottom, var(--elements-accent) 40%, var(--elements-accent-hover) 60%);
  border-radius: 4px;
}

.visually-hidden {
  @include visually-hidden;
}

.hidden-mobile {
  @include mobile {
    display: none !important;
  }
}

.hidden-mobile-s {
  @include mobile-s {
    display: none !important;
  }
}

.hidden-laptop {
  @include laptop {
    display: none !important;
  }
}

.visible-mobile-s {
  @include mobile-s-above {
    display: none !important;
  }
}

.visible-mobile {
  @include mobile-above {
    display: none !important;
  }
}

.visible-laptop {
  @include laptop-above {
    display: none !important;
  }
}

.ios::first-letter {
  text-transform: lowercase;
}

.text-gradient {
  background: var(--text-gradient);
  background-size: 200%;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: animate-gradient 5s linear infinite;
}

.preview-icon {
  width: fluid(60, 40);

  &--subtitle {
    width: fluid(46, 26)
  }
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus {
  border: var(--border-form);
  -webkit-text-fill-color: var(--text-basic) !important;
  -webkit-box-shadow: 0 0 0px 1000px var(--background-additional) inset;
  transition: background-color 5000s ease-in-out 0s;
}

.non-animated {
  animation-play-state: paused;
}

