.circle-btn {
  position: relative;
  width: 48px;
  height: 48px;
  border-radius: 50%;

  display: flex;
  align-items: center;
  justify-content: center;

  color: var(--text-basic);
  background-color: var(--elements-accent);

  overflow: hidden;

  svg {
    width: 24px;
    height: 24px;
    pointer-events: none;
    position: relative;
    z-index: 1;
  }

  path {
    fill: currentColor;
  }

  &::before {
    content: "";
    position: absolute;
    inset: 0;
    border-radius: inherit;

    background: linear-gradient(
      to bottom,
      var(--elements-accent-extra),
      var(--elements-accent)
    );

    opacity: 0;
    transition: opacity 0.4s ease-in-out;
    pointer-events: none;
  }

  @include hover {
    &::before {
      opacity: 1;
    }
  }
}

.circle-btn:focus-visible,
.services__article-button:focus-visible {
  outline: 2px solid var(--text-additional);
  outline-offset: 4px;
  border-radius: 50%;
}

[role=tab][aria-selected="true"] {
  color: var(--text-basic);
  background-color: var(--elements-accent);
  pointer-events: none;
}

[role=tabpanel][hidden="hidden"] {
  display: none;
}

// 1. Общие временные параметры
$fade-duration: 0.75s;
$move-duration: 0.9s;
$bezier-soft: cubic-bezier(0.22, 1, 0.36, 1);   // легкая инерция
$bezier-spring: cubic-bezier(0.34, 1.56, 0.64, 1); // мягкий overshoot

// 2. Fade-up (появление снизу)
.fade-up {
  opacity: 0;
  transform: translate3d(0, 40px, 0);
  transition:
  opacity $fade-duration $bezier-soft,
  transform $move-duration $bezier-spring;
  will-change: opacity, transform;

  &.faded {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }

  @include mobile {
    transform: translate3d(0, 25px, 0);

    &.faded {
      transform: translate3d(0, 0, 0);
    }
  }
}

// 3. Move-in (выезд слева)
.move-in {
  opacity: 0;
  transform: translate3d(-100%, 0, 0); // чуть больше для выразительности
  transition:
  opacity $fade-duration $bezier-soft,
  transform $move-duration $bezier-soft;
  will-change: opacity, transform;

  &.moved {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

/* Дополнительно: поддержка stagger через data-delay */
[data-delay] {
  transition-delay: attr(data-delay s, 0s); // SCSS не умеет attr() в transition-delay, так что delay задаётся через JS
}


