@use '../helpers/' as *;

.tabs {
    width: 100%;
    display: flex;
    flex-direction: column;
    row-gap: fluid(40, 20);

    &__header {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      column-gap: fluid(20, 10);
      row-gap: rem(10);
    }

    [role="tabpanel"] {
      opacity: 0;
      visibility: hidden;
      transition: opacity .25s ease;

      &.active {
        opacity: 1;
        visibility: visible;
      }
    }
}

  

  