@use '../helpers/' as *;

.select {
  width: rem(44);
  position: relative;
  user-select: none;
  z-index: 1;
  

  &.open {
    .select__dropdown {
      display: block;
      z-index: 1;
    }
  }

  &__input {
    @include square(rem(44));
    padding-inline: rem(8);
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    text-transform: uppercase;
    background-color: var(--elements-accent);
    color: var(--text-basic);
    border-radius: var(--radius-small);
    transition:background 0.4s ease-in-out;
    cursor: pointer;
    user-select: none;

    @include hover {
      background: linear-gradient(to bottom, var(--elements-accent-extra), var(--elements-accent));
      background-color: var(--elements-accent);
    }

    span {
      pointer-events: none; 
    }
  }

  &__backdrop {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    display: none;
    background-color: transparent;
    z-index: 0;
  }

  &__dropdown {
    max-height: 0;
    display: none;
    position: absolute;
    border: var(--border);
    border-radius: var(--radius-small);
    top: rem(48);
    left: 0;
    right: 0;
    z-index: -1;
    background-color: var(--background-basic);
    opacity: 0;
    transition: max-height var(--transition-duration) ease-in-out, opacity 0.6s ease-in-out;

    &.active {
      max-height: rem(500);
      opacity: 1;
    }
  }


  &__item {
    padding: rem(8);
    text-align: center;
    text-transform: uppercase;
    cursor: pointer;
    color: var(--text-additional);
    transition: color var(--transition-duration) ease-in-out;

    &.selected {
      color: var(--text-basic);
      pointer-events: none;
    }

    @include hover {
      color: var(--elements-accent);
    }
    &:not(:last-child) {
      border-bottom: var(--border);
    }

    &:first-child {
      border-radius: var(--radius-small) var(--radius-small) 0 0;
    }
    &:last-child {
      border-radius: 0 0 var(--radius-small) var(--radius-small);
    }
  }
}

  