@use '../helpers/' as *;

.slider {
  width: 100%;
  display: flex;
  flex-direction: column;

  &__top {
    margin-bottom: rem(20);
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__title {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      column-gap: fluid(20, 10);
      row-gap: rem(20);

      &-link {
        width: fluid(90, 80);
        height: fluid(40, 30);
        display: flex;
        justify-content: center;
        align-items: center;

        @include hover {
            .slider__title-link-icon {
                opacity: 0.6;
            }
        }

        &-icon {
            transition: opacity var(--transition-duration) ease-in-out;
        }
      }
  }

  &__container {
      display: flex;
      flex-direction: column;
      row-gap: rem(20);
  }

  &__content {
      width: 100%;
      height: auto !important;
      padding-inline: fluid(30, 10);
      padding-block: fluid(30, 20);
      display: flex;
      column-gap: fluid(100, 60);
      background-color: var(--background-additional);
      border: var(--border);
      border-radius: var(--radius);
      transition: var(--transition-with-border);

      @include hover {
        border-color: var(--border-hover);
        background-color: var(--background-basic);
      }

      @include laptop {
        column-gap: fluid(60, 40);
      }

      @include mobile {
        height: 100%;
        flex-direction: column;
        column-gap: 0;
      }
  }

  &__nav {
      flex: 1;               
      max-width: 33.33%;        
      display: flex;
      flex-direction: column;
      row-gap: fluid(30, 20);
      user-select: none;

      @include mobile {
        max-width: 100%;
        padding-bottom: fluid(40, 20);
        column-gap: rem(40);
        flex: none;
        flex-direction: row;
        flex-wrap: wrap;
      }
  }

  &__item {
      display: flex;
      align-items: center;
      gap: rem(4);
      cursor: pointer;
      transition: transform var(--transition-duration);
      -webkit-tap-highlight-color: transparent;
      
      &--active {
        pointer-events: none;
        .slider__dot {
          background-color: var(--elements-accent);
          transform: scale(1.4);
        }
        .slider__btn {
          color: var(--text);
          transform: scale(1.05);
        }
      }

      @include hover {
        .slider__dot {
          background-color: var(--elements-accent);
          transform: scale(1.4);
        }
        .slider__btn {
          color: var(--text-basic); 
          transform: translateX(1rem) scale(1.2);
        }
      }
  }

  &__dot {
        margin-right: fluid(20, 10);
        @include square(rem(12));
        border-radius: 50%;
        background-color: var(--text-additional);
        transition: background-color var(--transition-duration) ease-in-out, transform 0.4s ease-in-out;
        will-change: transform;
  }

  &__btn {
        transition: color var(--transition-duration) ease-in-out,transform 0.4s ease-in-out;
        color: var(--text-additional);
        will-change: transform;
        -webkit-tap-highlight-color: transparent;
  }
  
  &__screens {
      flex: 2;
      max-width: 66.66%;
      display: flex;
      overflow: hidden;
      position: relative;
        
      @include mobile {
        max-width: 100%;
        flex: none;
        flex-direction: column;
      }

      .swiper-wrapper {
        display: flex;
        align-items: stretch;
      }

      .swiper-slide {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        opacity: 0;
        pointer-events: none;
        transition: opacity 0.6s ease;
        will-change: opacity;
        backface-visibility: hidden;
        transform: translate3d(0, 0, 0);

        &.swiper-slide-active {
          opacity: 1;
          pointer-events: auto;
        }
      }

    &-item {
      padding-inline: fluid(30, 10);
      padding-block: fluid(30, 20);
      background: var(--background-form-field);
      border-radius: var(--radius);
      border: var(--border-form);
      box-shadow: var(--shadow);
    }

    &-description {
      align-self: flex-start;
      margin-bottom: 0;
      padding-bottom: fluid(20, 10);
      position: relative;
      flex-grow: 1;
      font-size: rem(14);
      opacity: 0;
      transform: translate3d(0, 10px, 0);
      transition: opacity 0.6s ease, transform 0.6s ease;
      will-change: opacity, transform;
      backface-visibility: hidden;

      .swiper-slide-active & {
        opacity: 1;
        transform: translate3d(0, 0, 0);
      }

      &::after {
        content: '';
        width: 100%;
        height: rem(2);
        position: absolute;
        bottom: 0;
        left: 0;
        background-image: linear-gradient(
          90deg,
          rgba(15, 17, 35, 0) 0%,
          var(--elements-accent-pale) 50%,
          rgba(15, 17, 35, 0) 100%
        );
      }
    }

    &-image {
      margin-top: auto;
      padding-top: fluid(40, 20);
      display: flex;
      justify-content: center;
      align-items: flex-end;
      position: relative;
      opacity: 0;
      transform: translate3d(0, 20px, 0);
      transition: opacity 0.6s ease, transform 0.6s ease;
      will-change: opacity, transform;
      backface-visibility: hidden;

      .swiper-slide-active & {
        opacity: 1;
        transform: translate3d(0, 0, 0);
      }
      
      img {
        width: min(rem(520), 100%);
        // height: auto;
        border-radius: var(--radius);
        object-fit: contain;
        -webkit-user-drag: none;
        backface-visibility: hidden;
        transform: translate3d(0, 0, 0);
      }

      &--mobile {
        img {
          width: min(rem(180), 100%);
        }
      }
    }
  }

  @media (prefers-reduced-motion: reduce) {
    &__screens .swiper-slide,
    &__screens-description,
    &__screens-image,
    &__dot,
    &__btn {
      transition: none !important;
      transform: none !important;
      animation: none !important;
    }
  }
}

