@use '../helpers/' as *;

.accordion {
  width: 100%;
  &__details {
    &[open] {
      .accordion__title {
        // min-height: 0;
        &::before,
        &::after {
          background-color: var(--elements-accent);
        }

        &::after {
          rotate: 0deg;
        }
      }

      & + .accordion__content {
        grid-template-rows: 1fr;
      }
    }
  }

  &__summary {
    color: var(--text-basic);
    transition: color var(--transition-duration) ease-in-out;
    list-style: none;
    cursor: pointer;
    user-select: none;

    @include hover {
      color: var(--text-additional);
        .accordion__title::before,
        .accordion__title::after {
          background-color: var(--elements-accent);
      }
    }

    &::-webkit-details-marker {
      display: none;
    }
  }

  &__title {
    display: flex;
    align-items: center;
    position: relative;
    min-height: var(--accordionListCounterHeight);
    padding-right: rem(40);
    color: inherit;
    transition-duration: var(--transition-duration);

    &::before,
    &::after {
      @include abs-y;

      content: '';
      right: 0;
      width: fluid(20, 16);
      height: rem(2);
      background-color: var(--text-basic);
      border-radius: rem(2);
      transition-duration: var(--transition-duration);
    }

    &::after {
      rotate: 90deg;
    }
  }

  &__content {
    display: grid;
    grid-template-rows: 0fr;
    color: var(--text-additional);
    transition: grid-template-rows var(--transition-duration) ease-out;

    &-inner {
      overflow: hidden;
    }

    &-body {
      margin-top: fluid(24, 16);
      padding-right: fluid(60, 40);
    }
  }
}
