@use '../helpers/' as *;

.clients {
  &__body {
    width: 88%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr;
    align-self: start;
    row-gap: fluid(40, 20);

    @include laptop {
      width: 100%;
    }

    @include mobile {
      row-gap: fluid(60, 40);
    }
  }

  &__content {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: fluid(80, 60);
    margin-bottom: rem(40);

    @include laptop {
      column-gap: fluid(60, 40);
      margin-bottom: rem(20);
    }

    @include mobile {
      grid-template-columns: 1fr;
      row-gap: rem(10);
      margin-bottom: 0;
    }
  }

  &__content > * {
    min-width: 0;
  }

  &__text {
    width: 100%;
    min-width: 0;
    position: relative;
    overflow: hidden;
    isolation: isolate;
    touch-action: pan-y;
    will-change: height;

    /* Fade на уровне slide */
    &.swiper-fade .swiper-slide {
      transition-property: opacity;
      transition-duration: 2500ms;
      transition-timing-function: ease-in-out;
      will-change: opacity;

      /* НЕ трогаем transform — Swiper Fade использует его */
      backface-visibility: hidden;
      -webkit-backface-visibility: hidden;
    }
  }

  /* ВАЖНО: swiper-autoheight больше не нужен и конфликтует с JS-фиксированной высотой */
  /* УДАЛЕНО:
  &__text.swiper-autoheight,
  &__text.swiper-autoheight .swiper-wrapper {
    height: auto !important;
  }
  */

  &__text-wrapper {
    height: 100%;
  }

  &__text-slide {
    height: 100%;
    display: block;
  }

  &__text-inner {
    height: auto;
    min-height: 0;
    display: flex;
    flex-direction: column;
    row-gap: fluid(40, 20);
    overflow-wrap: anywhere;

    @include laptop {
      row-gap: fluid(20, 10);
    }
  }

  &__slider {
    position: relative;
    width: 100%;
    height: auto;

    aspect-ratio: 5 / 3;

    max-height: 75vh;
    @supports (height: 1svh) {
      max-height: 75svh;
    }
    @supports (height: 1dvh) {
      max-height: 75dvh;
    }

    overflow: hidden;
    isolation: isolate;

    @media (min-aspect-ratio: 21 / 9) {
      min-height: 60vh;
      @supports (height: 1svh) {
        min-height: 60svh;
      }
      @supports (height: 1dvh) {
        min-height: 60dvh;
      }
    }

    @include mobile-s {
      aspect-ratio: 4 / 3;

      max-height: 65vh;
      @supports (height: 1svh) {
        max-height: 65svh;
      }
      @supports (height: 1dvh) {
        max-height: 65dvh;
      }
    }
  }

  &__video-container {
    width: 100%;
    height: 100%;
    position: relative;
    background-color: var(--background-basic);
    overflow: hidden;
    z-index: 0;
    isolation: isolate;
  }

  &__video {
    position: absolute;
    inset: 0;

    width: 100%;
    height: 100%;

    object-fit: cover;
    object-position: center;

    display: block;
    pointer-events: none;
    z-index: 1;

    backface-visibility: hidden;

    &::-webkit-media-controls,
    &::-webkit-media-controls-start-playback-button {
      display: none !important;
      -webkit-appearance: none;
    }
  }

  &__video-blend {
    position: absolute;
    inset: 0;
    background: var(--background-basic);
    mix-blend-mode: lighten;
    pointer-events: none;
    z-index: 2;
  }

  &__slider-wrapper {
    position: absolute;
    inset: 0;
    z-index: 10;
    isolation: isolate;
    touch-action: pan-y;

    .swiper-wrapper {
      transform: translate3d(0, 0, 0);
      will-change: transform;
    }
  }

  &__slider-list {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
  }

  &__slider-item {
    display: flex;
    align-items: center;
    justify-content: center;

    opacity: 0.4;
    transition: opacity 0.5s var(--slide-transition), transform 2s var(--slide-transition);

    &.swiper-slide-active {
      opacity: 1;
      transform: scale(1);
      z-index: 10;
    }

    &.swiper-slide-next,
    &.swiper-slide-prev {
      transform: scale(0.6);
    }
  }

  &__logo {
    position: relative;

    width: clamp(140px, 20vw, 200px);
    min-height: clamp(60px, 12vw, 110px);

    display: flex;
    align-items: center;
    justify-content: center;

    padding: 0;
    border: 0;
    background: transparent;

    cursor: pointer;
    overflow: hidden;

    -webkit-tap-highlight-color: transparent;
    touch-action: manipulation;
  }

  &__logo,
  &__logo * {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    user-select: none;
  }

  &__logo-half {
    position: absolute;
    inset: 0;

    display: flex;
    justify-content: center;
    align-items: center;

    perspective: 150px;
    transform-style: preserve-3d;
  }

  &__logo-half--left {
    perspective-origin: left center;
    -webkit-clip-path: polygon(0 0, 50% 0, 50% 100%, 0 100%);
    clip-path: polygon(0 0, 50% 0, 50% 100%, 0 100%);

    .clients__logo-img {
      animation: image-left 1s ease-in-out infinite alternate;
    }
  }

  &__logo-half--right {
    perspective-origin: right center;
    -webkit-clip-path: polygon(50% 0, 100% 0, 100% 100%, 50% 100%);
    clip-path: polygon(50% 0, 100% 0, 100% 100%, 50% 100%);

    transform: translateX(-1px);

    .clients__logo-img {
      animation: image-right 1s ease-in-out infinite alternate;
    }
  }

  &__logo-img {
    max-height: clamp(60px, 6vw, 92px);
    width: auto;
    object-fit: contain;

    backface-visibility: hidden;
    -webkit-backface-visibility: hidden;
    transform: translateZ(0);

    user-select: none;
    pointer-events: none;
  }

  &__slider-item,
  &__logo {
    backface-visibility: hidden;
    -webkit-backface-visibility: hidden;
    transform: translateZ(0);
  }

  &__facts {
    width: 100%;
    margin-bottom: fluid(60, 20);
  }

  &__reviews {
    width: 100%;
    display: flex;
    flex-direction: column;
  }
}
