@use '../helpers/' as *;

.footer {
  background-color: var(--background-additional);
  @include fluid-text(16, 14);

  &__body {
    padding-block: fluid(40, 30);
    border-bottom: var(--border);
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-self: start;
    column-gap: fluid(80, 40);

    @include laptop {
      padding-bottom: fluid(30, 20);
      grid-template-columns: repeat(2, 1fr);
      row-gap: rem(40);
    }

    @media (width <= rem(539)) {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__column {
    display: grid;
    row-gap: fluid(30, 16);
    align-self: start;

    &:nth-child(2) {
      justify-content: start;

      @include laptop {
        justify-content: center;
      }
    }

    &:nth-child(3) {
      justify-content: center;
    }

    &:nth-child(4) {
      justify-content: end;
      @include laptop {
        grid-column: -1 / 1;
        place-items: center;
        justify-content: center;

        .footer__list {
          grid-template-columns: repeat(2, 1fr);
          column-gap: 1rem;
        }
      }
    }
  }

  &__column-title {
    @media (width <= rem(539)) {
      text-align: center;
    }
  }

  &__list {
    display: grid;
    row-gap: fluid(18, 10);

    &--info {
      grid-template-columns: max-content;
    }
  }

  &__item-term {
    margin-right: 1rem;
    font-family: var(--font-family-titles);
  }

  &__item-description {
    margin: 0;
    grid-column-start: 2;
  }

  &__item {
    &--address {
      display: flex;
      align-items: center;
    }
  }

  &__link {
    position: relative;
    @include hover {
      .footer__icon {
        fill: var(--text-basic);
        transition-duration: var(--transition-duration);
      }
    }
    &--contact {
      display: flex;
      align-items: center;
    }
  }

  &__icon {
    @include square(rem(25));
    margin-top: rem(-4);
    margin-right: 1rem;
    // fill: var(--elements-accent);
    color: var(--elements-accent);

    &--developer {
      margin-inline: 0.2rem;
    }
    &--copyright {
      @include square(rem(16));
      margin: 0;
      fill: currentColor;
    }
  }

  &__extra {
    padding-block: fluid(30, 20);
    display: flex;
    align-items: center;
    flex-direction: column;
    row-gap: 1rem;
  }

  &__developer {
    display: flex;
    align-items: center;

    @include hover {
      .footer__icon--developer {
        fill: var(--text-basic);
        transition-duration: var(--transition-duration);
        animation: rotating-icon 0.8s ease-in-out;
      }
    }
  }

  &__copyright {
    display: flex;
    align-items: center;
    column-gap: 0.2rem;
    color: var(--text-basic);
  }
}

