@use '../helpers/' as *;

.ios {
  &__body {
    width: 88%;
    margin: 0 auto;

    @include laptop {
      width: 100%;
    }
  }

  &__top {
    margin-bottom: fluid(40, 20);
    display: grid;
    grid-template-columns: 1fr;
    align-self: start;
    row-gap: fluid(40, 20);
  }

