@use '../helpers/' as *;

.facts {
  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: fluid(80, 20);
    

    @include laptop {
      column-gap: fluid(30, 10);
      place-self: normal;
    }

    @include mobile {
      row-gap: fluid(40, 20);
      grid-template-columns: repeat(2, 1fr);
    }

    @include mobile-s {
      grid-template-columns: 1fr;
    }
  }

  &__item {
    &:nth-child(3n) {
      @include mobile {
        grid-column: -1 / 1;
      }
    }  
  }

  &__item-info {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    gap: rem(10);
    padding: fluid(30, 12);
    background-color: var(--background-additional);
    border: var(--border);
    border-radius: var(--radius);
  }

  &__item-description {
    display: inline-block;
    width: fluid(120, 64);
    text-align: center;
    position: relative;
    background-image: linear-gradient(180deg, var(--elements-accent) 0%, rgba(87, 71, 235, 0.4) 90%); 
    background-size: 100%;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

    &::before,
    &::after {
      content: '';
      width: fluid(20, 16);
      height: rem(2);
      position: absolute;
      display: block;
      top: 50%;
      right: rem(-24);
      translate: 0 -50%;
      background-color: var(--text-basic);
      border-radius: rem(2);
      transition-duration: var(--transition-duration);
    }

    &::after {
      rotate: 90deg;
    }

    &--years {
      width: fluid(80, 43);
    }
  }

  &__icon {
    @include square(fluid(26, 22));
    margin-top: rem(-15);
    fill: var(--text-basic);
  } 
}

