@use '../helpers/' as *;

.burger-button {
  @include square(rem(44));
  position: relative;
  background-color: var(----background-additional);
  border: var(--border);
  border-radius: var(--radius-small);

  &::before,
  &::after {
    content: '';
    width: 60%;
    height: rem(3);
    background-color: currentColor;
    border-radius: rem(2);
    transform-origin: center;
    transition: all var(--transition-duration) ease-in-out;
  }

  &::before {
    position: absolute;
    top: 26%;
    left: 50%;
    transform: translateX(-50%);
    box-shadow: 0 8px 0 0 var(--text-basic), 0 16px 0 0 var(--text-basic);
  }

  &::after {
    @include abs-center;
    visibility: hidden;
    opacity: 0;
  }

  &.is-active {
    &::before {
      @include abs-center;
      transform: rotate(45deg);
      box-shadow: none;
    }
    &::after {
      visibility: visible;
      opacity: 1;
      transform: rotate(-45deg);
    }
  }

  @include hover {
    background-color: var(--background-basic);
    border-color: var(--border-hover);
    box-shadow: var(--shadow);
  }
}
