@use '../helpers/' as *;

.microsoft {
  &__body {
    width: 88%;
    margin: 0 auto;
    
    @include laptop {
      width: 100%;
    }
  }
    
  &__questions {
    margin-bottom: fluid(100, 60);
    display: grid;
    grid-template-columns: 1fr 2fr;
    // column-gap: rem(100);
    column-gap: fluid(100, 60);
    
    @include laptop {
      column-gap: fluid(60, 40);
    }

    @include mobile {
      grid-template-columns: 1fr;
      row-gap: fluid(60, 40);
    }
  }

  &__article {
    display: grid;
    grid-template-columns: 1fr;
    align-self: start;
    row-gap: fluid(40, 20);
  }

  &__preview-description {
    margin-bottom: 0;
  }

  &__preview-button {
    margin-right: auto;
  }

  &__accordion-list {
    display: grid;
    row-gap: fluid(40, 30);

    @include mobile {
      row-gap: fluid(20, 16);
    }

    &--has-counter {
      --accordionListCounterHeight: #{fluid(52, 44)};
      counter-reset: custom-counter;
    
      .microsoft__accordion-item {
        display: flex;
        column-gap: fluid(20, 16);
        counter-increment: custom-counter;
        &::before {
          @include flex-center;
          content: counter(custom-counter, decimal-leading-zero);
          min-width: #{fluid(54, 46)};
          height: var(--accordionListCounterHeight);
          padding-top: rem(3);
          background-color: var(--background-additional);
          @include fluid-text(20, 18);
          font-weight: 700;
          border-radius: var(--radius-small);
          border: var(--border);
        }
      }
    }
  }
  &__accordion-item {
    padding-block: 1rem;
    &:not(:last-child) {
      position: relative;

      &::after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;

        height: rem(2);
        background-image: linear-gradient(
          90deg,
          rgba(15, 17, 35, 0) 0%,
          var(--elements-accent-pale) 50%,
          rgba(15, 17, 35, 0) 100%
        );
      }
    }
  }

