@use '../helpers/' as *;

.form {
  display: grid;
  column-gap: fluid(20, 16);
  row-gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
  padding-inline: fluid(30, 10);
  padding-block: fluid(30, 20);
  background-color: var(--background-additional);
  border: var(--border);
  border-radius: var(--radius);
  transition: var(--transition-with-border);

  @include laptop {
    column-gap: fluid(16, 8);
  }

  @include mobile-s {
    grid-template-columns: repeat(1, 1fr);
  }

  @include hover {
      border-color: var(--border-hover);
      background-color: var(--background-basic);
    }

  &__cell {
    &--wide {
      grid-column: -1 / 1;
    }
  }

  &__submit-button {
    width: 40%;
    max-width: rem(200);
    justify-self: center;
  }
}
