@use '../helpers/' as *;

@property --angle {
  syntax: '<angle>';
  initial-value: 0deg;
  inherits: false;
}

:root {
  --line-height: 1.5em;
  --lines: 5;                   /* сколько строк показывать в свернутом виде */
  --collapsed: calc(var(--line-height) * var(--lines));
  --expanded: 100%;            /* «максимальная» высота при раскрытии */
  --transition-time: 0.65s;           
}

.services {
  &__body {
    width: 88%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-self: start;
    row-gap: fluid(60, 40);

    @include laptop {
      width: 100%;
    }
  }

  &__top {
    display: flex;
    flex-direction: column;
    row-gap: fluid(40, 20);

    &-title {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      column-gap: fluid(20, 10);
        row-gap: rem(20);
    }
  }

  &__preview-description p:first-child {
    margin-bottom: 1rem;
  }

  &__list {
    width: 100%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(min(100%, rem(360)), 1fr));
    // column-gap: rem(100);
    column-gap: fluid(100, 60);
    row-gap: fluid(40, 20);

    @include laptop {
      column-gap: fluid(60, 40);
    }
    @include mobile-s {
      grid-template-columns: repeat(auto-fit, minmax(min(100%,rem(280)), 1fr));
      column-gap: fluid(40, 20);
    }
  }

  &__article {
    display: flex;
    flex-direction: column;
    align-self: start;
    padding: fluid(30, 20);
    row-gap: 1rem;
    background-color: var(--background-additional);
    border: var(--border);
    border-radius: var(--radius);
    transition: var(--transition-with-border);

    @include hover {
      border-color: var(--border-hover);
      background-color: var(--background-basic);

      .services__article-icon::after {
        animation: 1s spin linear 1;
      }
    }

    &-top {
      padding-bottom: rem(10);
      display: flex;
      align-items: center;
      column-gap: fluid(20, 16);
      position: relative;
      z-index: 1;

      &::after {
        width: 100%;
        height: rem(2);
        content: '';
        position: absolute;
        bottom: 0;
        right: 0;
        background-image: linear-gradient(
          90deg,
          rgba(15, 17, 35, 0) 10%,
          var(--elements-accent-pale) 50%,
          rgba(15, 17, 35, 0) 100%
        );
      }
    }

    &-icon {
      max-width: fluid(70, 60);
      max-height: fluid(70, 60);
      padding: 1rem;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      background-color: var(--background-additional);
      border-radius: 50%;

      &::after {
        content: '';
        position: absolute;
        inset: -0.12rem;
        z-index: -1;
        background-image: conic-gradient(
          from var(--angle),
          transparent 25%,
          var(--elements-accent),
          var(--elements-accent-additional)
        );
        border-radius: 50%;
      }
    }

    &-image {
      position: relative;
      fill: var(--text-basic);
      z-index: 2;
    }

    &-text {
      position: relative;
      max-height: var(--collapsed);
      overflow: hidden;
      line-height: var(--line-height);
      color: var(--text-additional);
      transition: color 0.6s ease-in-out, max-height 0.8s var(--slide-transition);
      cursor: pointer;

      &::after {
        content: '. . .';
        position: absolute;
        bottom: 0;
        right: 0;
        padding-left: 0.2em;
        height: var(--line-height);
        display: inline-block;
        opacity: 1;
        transition: opacity 0.3s ease;
        pointer-events: none;
      // background: linear-gradient(to right, rgba(255,255,255,0), #fff 60%);
        color: var(--text-additional);
    }

      @include hover {
        max-height: var(--expanded);
        color: var(--text-basic);

        &::after {
          opacity: 0;
        }
      }

      @include laptop {
        overflow: hidden;
        line-height: 1.5;
        transition: max-height var(--transition-time) var(--slide-transition);
        position: relative;
        color: var(--text-basic);

        &.clamped {
           display: -webkit-box;
          -webkit-box-orient: vertical;
        }

        &::after {
          content: '';
        }
      }

      & p {
        margin: 0;
      }
      
    }

    &-button {
      display: none;

      @include laptop {
        display: flex;
        align-self: end;
        position: relative;
        width: 48px;
        height: 48px;
        border-radius: 50%;
        background-color: var(--elements-accent);

        &::before,
        &::after {
          @include abs-center;
          content: '';
          width: fluid(20, 16);
          height: rem(2);
          background-color: var(--text-basic);
          border-radius: rem(2);
          transition: rotate var(--transition-duration) ease-in-out;
        }

        &::after {
          rotate: 90deg;
        }
      }

      &.open {
        &::after {
          // width: 0;
          rotate: 0deg;
        }
      }
    }
  }
}

