.hero {
  position: relative;
  display: flex;
  align-items: center;

  // Базовая логика высоты (важно для iOS)
  // 1) fallback: vh
  // 2) предпочтительно для "не прыгающего" hero: svh
  // 3) при необходимости динамики: dvh (можно оставить как поддержку)
  min-height: 100vh;

  @supports (min-height: 1svh) {
    min-height: 100svh;
  }

  @supports (min-height: 1dvh) {
    // Если хочешь максимально “вписываться” при появлении/скрытии UI,
    // оставь dvh. Если цель — абсолютная стабильность без подскоков,
    // можно НЕ переопределять svh на dvh.
    min-height: 100dvh;
  }

  // Твой особый кейс: низкий landscape
  @media (orientation: landscape) and (max-height: 500px) {
    // Тут важнее обеспечить помещаемость контента
    min-height: rem(480);
  }

  &__video {
    // position: absolute;
    // inset: 0;
    // width: 100%;
    // height: 100%;

    // opacity: 0.6;
    // visibility: hidden;

    // // для видео на iOS: предотвращает “всплытие” inline-элемента
    // display: block;
    // object-fit: cover;
    // object-position: center;
    // pointer-events: none;
    position: absolute;
    inset: 0;
    width: 100%;
    height: 100%;

    display: block;
    object-fit: cover;
    object-position: center;

  // важно для iOS при fade-in
    backface-visibility: hidden;
    transform: translateZ(0); // добавляй только если видишь мерцание
    will-change: opacity;
  }

  &__background {
    // position: absolute;
    // inset: 0;
    // height: 100%;

    // width: 0%;
    // background-color: var(--background-basic);
    // transition: width 0.5s var(--slide-transition);
    position: absolute;
    inset: 0;
    background-color: var(--background-basic);

    // вместо width: 0%;
    transform: scaleX(0);
    transform-origin: left center;

    // если нужен плавный “шторкой” эффект
    will-change: transform;
  }

  &__container {
    width: 100%;
    position: relative;
    z-index: 2;

    display: flex;
    flex-direction: column;
    row-gap: rem(60);

    @media (orientation: landscape) and (max-height: 500px) {
      row-gap: rem(20);

      .hero__description {
        font-size: rem(20);
      }
    }
  }

  &__title {
    display: flex;
    flex-direction: column;
  }

  &__title-name {
    line-height: 0;
  }

  &__title-stroked {
    width: 100%;
    max-width: 780px;
    height: auto;
    display: block;
    margin-bottom: fluid(20, 10);

    @include laptop {
      max-width: 400px;
    }

    @include mobile {
      max-width: 320px;
    }

    path {
      fill: none;
      stroke: var(--text-basic);
      stroke-width: 1;
      stroke-linecap: round;
      stroke-linejoin: round;
      vector-effect: non-scaling-stroke;
      stroke-dasharray: 0;
      stroke-dashoffset: 0;
    }
  }

  &__subtitle {
    text-transform: uppercase;
    color: var(--elements-accent);

    &.non-animated {
      animation-play-state: paused;
    }
  }

  &__description {
    display: flex;
    flex-direction: column;
    row-gap: rem(6);
  }

  &__animation-space {
    --size: 20px;
    position: absolute;
    inset: 0;
    z-index: 1;

    width: 100%;
    height: 100%;
    opacity: 1;

    background-color: var(--background-basic);
    background: conic-gradient(
      from 180deg at 50% 70%,
      var(--text-basic) 0deg,
      var(--background-additional) 72.0000010728836deg,
      var(--elements-accent-additional) 144.0000021457672deg,
      var(--elements-accent) 144.0000021457672deg,
      var(--elements-accent-extra) 216.00000858306885deg,
      var(--elements-accent-extra2) 288.0000042915344deg,
      var(--text-basic) 1turn
    );

    mask: radial-gradient(circle at 50% 50%, white 2px, transparent 2.5px) 50% 50% / var(--size) var(--size),
      url('../img/noise-mask.webp') 256px 50% / 256px 256px;
    mask-composite: intersect;

    animation: flicker 20s infinite linear;
    animation-play-state: running;

    &.non-animated {
      animation-play-state: paused;
    }
  }
}

