@keyframes spin {
  from {
    --angle: 0deg;
  }
  to {
    --angle: 360deg;
  }
}

@keyframes boxShadow {
  to {
    box-shadow: var(--shadow);
  }
}

@keyframes rotating-icon {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes flicker {
  to {
    mask-position: 50% 50%, 0 50%;
  }
}

