@keyframes starFly {
  0% {
    transform: translateY(0) rotate(0deg);
    opacity: 0;
  }
  10% {
    opacity: 1; /* быстро появляется */
  }
  50% {
    opacity: 1; /* летит полностью видимая */
  }
  60% {
    opacity: 0.8; /* начинает исчезать чуть раньше конца */
  }
  75% {
    opacity: 0.5; 
  }
  90% {
    opacity: 0.2; 
  }
  95% {
    opacity: 0.1; 
  }
  100% {
    transform: translateY(calc(-1 * var(--fly-distance, 200px))) rotate(360deg);
    opacity: 0;
  }
}
