@use '../helpers/' as *;

.checkbox {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  column-gap: 1rem;
  row-gap: 1rem;

  @include laptop {
    column-gap: fluid(16, 8);
  }

  @include mobile-s {
    column-gap: rem(10);
  }

  &__input {
    --size: #{rem(46)};

    @include square(var(--size));
    @include flex-center;

    appearance: none;
    margin: 0;
    position: relative;
    background-color: var(--background-form-field);
    border: var(--border-form);
    border-radius: var(--radius-small);
    transition: var(--transition-with-border);

    &:not(:checked) {
      &::after {
        @include hide;
      }
    }

    @include hover {
      border-color: var(--border-hover);
      background-color: var(--background-additional);
    }

    &:user-invalid {
      border-color: var(--error);
    }

    &::after {
      content: '';
      @include square(rem(31));
      position: absolute;
      top: rem(4);
      left: rem(5);
      background-image: url(../img/svg/icon-check.svg);
      background-repeat: no-repeat;
      background-position: center center;
      background-size: contain;
      transform: scale(0);
      transition: transform 0.3s ease-in-out;
    }

    &:checked {
      border-color: var(--border-hover);
      background-color: var(--background-additional);
      &::after {
        transform: scale(1);
      }
    }
  }
}
