@use '../helpers/' as *;

.section {
  padding-top: fluid(160, 100);
  overflow-x: clip;
  @supports not (overflow: clip) {
    overflow-x: hidden;
  }
  
  &__title {
    margin-bottom: fluid(60, 40);
    font-family: var(--font-family-titles);
    text-transform: uppercase;
    letter-spacing: 1.4px;
    -webkit-text-stroke: rem(0.8) var(--text-basic);
    color: transparent;
  }

  &--contact {
    padding-bottom: fluid(60, 40);
  }

  @media (orientation: landscape) and (max-height: 500px) {
    &--hero{ 
      padding-top: rem(60);
    }
  }
}
