@use '../helpers/' as *;

.swiper-pagination.swiper-pagination-bullets {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  column-gap: rem(2);
}

.swiper-pagination-bullet {
  width: rem(20);
  height: rem(4);
  border-radius: rem(2);
  background-color: var(--elements-accent-pale-more);
  transition: background-color var(--transition-duration) ease-in-out;

  &-active {
    background: var(--elements-accent);
  }
}